import React, { useEffect, useState } from "react";
import {
  ImgBox,
  ImgBoxContainer,
  Image,
  ImageBtnPre,
  ImageBtnNext,
} from "./boarddetailPc.styled";


// const images = [
//   "data:image/jpeg;base64,",
//   "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT65flMGudllD-IqpUkZSOVkpi2sIxIx9M1EQ&usqp=CAU",
//   "data:image/jpeg;base64,",
// ];

const ImgSlicePc = ({images}) => {
  const imgPath = '/imgs/userplanimg'
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  const prevImage = () => {
    setCurrentImageIndex((prevIndex) =>
      prevIndex === 0 ? images.length - 1 : prevIndex - 1
    );
  };

  const nextImage = () => {
    setCurrentImageIndex((prevIndex) =>
      prevIndex === images.length - 1 ? 0 : prevIndex + 1
    );
  };

  useEffect(() => {
    console.log(images)
  }, [])

  return (
    <>
      <ImgBoxContainer>
        <ImageBtnPre onClick={prevImage}>◀️</ImageBtnPre>
        <ImgBox
          style={{
            transform: `translateX(-${
              currentImageIndex * (100 / images.length)
            }%)`,
          }}
        >
          {images.map((image, index) => (
            <Image
              key={index}
              src={`${imgPath}/${image}`}
              alt={`Image ${index + 1}`}
            />
          ))}
        </ImgBox>
        <ImageBtnNext onClick={nextImage}>▶️</ImageBtnNext>
      </ImgBoxContainer>
    </>
  );
};

export default ImgSlicePc;
